import React from "react";
import Heading from "../Heading";
import FullListData from "./FullListData";
import Display from "./Display";
import "./Internship.css";

function ncard(val) {
  return (
    <Display
      key={val.id}
      tid={val.id}
      title={val.title}
      location={val.Location}
      ApplyLink={val.ApplyLink}
      des={val.description}
      type={val.type}
      typeTag={val.typeTag}
      hiringOrganization={val.hiringOrganization["name"]}
      logo={val.hiringOrganization["logo"]}
      org={val.identifier["name"]}
    />
  );
}

const BrowseAllIntern = () => {
  return (
    <>
      <section id="toppickjob">
        <div className="container">
          <div className="row justify-content-center">
            <Heading
              title="Trending Internships"
              para="Top Internship Where You Can Apply"
            />
            {FullListData.map(ncard)}
          </div>
        </div>
      </section>
    </>
  );
};

export default BrowseAllIntern;
